
.select-number{background:#ffffff;min-height:1420px
}
.select-number .step{padding:10px 20px
}
.select-number .step .step-content{padding:60px 10% 120px;border-bottom:#f3f3f3 solid 1px
}
.select-number .step .step-content .el-icon-upload{background:url("../../../assets/img/self-icon.png") no-repeat;background-position:-192px -232px;position:absolute;top:-15px;left:-12px;width:65px;height:65px
}
.select-number .step .step-content .el-icon-edit{background:url("../../../assets/img/self-icon.png") no-repeat;background-position:-192px -2px;position:absolute;top:-15px;left:-12px;width:65px;height:65px
}
.select-number .step .step-content .el-icon-picture{background:url("../../../assets/img/self-icon.png") no-repeat;background-position:-112px -77px;position:absolute;top:-15px;left:-12px;width:65px;height:65px
}
.select-number .step .step-content .el-icon-document{background:url("../../../assets/img/self-icon.png") no-repeat;background-position:-110px -157px;position:absolute;top:-15px;left:-12px;width:65px;height:65px
}
.select-number .step .step-content .el-icon-upload:before{content:""
}
.select-number .step .step-content .el-icon-edit:before{content:""
}
.select-number .step .step-content .el-icon-picture:before{content:""
}
.select-number .step .step-content .el-icon-document:before{content:""
}
.select-number .step .step-content .is-horizontal{height:4px
}
.select-number .step .step-content .is-horizontal .el-step__line-inner{border-width:2px;border-color:#f47300
}
.select-number .step .step-content .el-step__main{padding-top:20px
}
.select-number .step .step-content .el-step__main .is-finish{font-weight:500;color:#333333
}
.select-number .step .step-content .el-step__main .is-process{font-weight:500;color:#333333
}
.select-number .step .step-content .el-step__main .is-wait{font-weight:500;color:#333333
}
.select-number .searchBar{margin:25px 20px
}
.select-number .searchBar .el-icon-caret-top:before{content:none
}
.select-number .searchBar .el-icon-loading:before{content:none
}
.select-number .searchBar .el-select .el-input .el-input__icon{background:url("../../../assets/img/subscri-icon.png") no-repeat left center;background-position:-70px -77px;width:12px;height:8px;right:13px
}
.select-number .searchBar .el-input{max-width:270px;margin-right:5px
}
.select-number .searchBar .el-input .el-input__inner{border-radius:0;height:35px
}
.select-number .searchBar .el-button{width:60px;height:35px;border-radius:0;background:#10a8f8;position:relative
}
.select-number .searchBar .search-icon{background:url("../../../assets/img/self-icon.png") no-repeat;background-position:-4px -277px;position:absolute;top:6px;left:16px;width:25px;height:25px
}
.select-number .searchBar .fresh-icon{background:url("../../../assets/img/self-icon.png") no-repeat;background-position:-4px -305px;position:absolute;width:25px;height:30px;margin-left:10px
}
.select-number .searchBar span{font-size:16px;margin-left:15px;cursor:pointer
}
.select-number .searchBar .change{height:35px;line-height:35px;float:right;margin-right:35px;cursor:pointer
}
.select-number .numberList{margin:0 20px 20px 20px;border-left:1px solid #dcdbdb;border-top:1px solid #dcdbdb
}
.select-number .numberList .number{width:20%;float:left;height:100px;line-height:100px;border-right:1px solid #dcdbdb;border-bottom:1px solid #dcdbdb;display:inline-block;text-align:center;font-size:20px;cursor:pointer;position:relative
}
.select-number .numberList .number .selected-icon{background:url("../../../assets/img/self-icon.png") no-repeat;background-position:5px -351px;position:absolute;bottom:0;right:0;width:45px;height:45px;display:''
}
.select-number .cardList{margin:5px;font-size:14px
}
.select-number .cardList .title{background:url("../../../assets/img/dian.png") no-repeat left center;background-size:6px;margin-left:15px;font-size:16px
}
.select-number .cardList .title p{padding-left:15px;height:60px;line-height:60px
}
.select-number .cardList .card-type{margin-left:15px
}
.select-number .cardList .card-type .card{width:130px;float:left;margin-right:25px;height:80px;line-height:80px;border:1px solid #dcdbdb;display:inline-block;text-align:center;cursor:pointer;position:relative
}
.select-number .cardList .card-type .card .selected-icon{background:url("../../../assets/img/self-icon.png") no-repeat;background-position:-3px -424px;bottom:0;right:0;position:absolute;width:30px;height:30px;display:''
}
.select-number .cardList .card-type .card-desc{width:130px;float:left;margin-right:25px;margin-top:10px;height:20px;line-height:20px;text-align:center
}
.select-number .check{margin:20px;background:#f5f5f5
}
.select-number .check .tips{font-size:16px;height:60px;line-height:60px;margin-top:15px;margin-left:15px
}
.select-number .check .check-item{color:#fff;height:130px;background:#32c5d2;max-width:350px;text-align:center;margin:0 15px 30px 15px
}
.select-number .check .check-item .item-left{margin-top:55px;font-size:14px
}
.select-number .check .check-item .item-right{margin:15px 0;height:95px;border-left:1px solid #23a6b1
}
.select-number .check .check-item .item-right p{padding-top:20px;font-size:14px
}
.select-number .check .check-item .item-right .item-price{font-size:20px;margin-top:-15px
}
.select-number .check .check-item .item-right .item-number{font-size:20px;padding-top:35px
}
.select-number .check .check-item .item-right .item-card{padding-top:15px
}
.select-number .check .check-item .border2{border-left:1px solid #c8202b
}
.select-number .check .check-item .border3{border-left:1px solid #2178b4
}
.select-number .check .theme2{background:#e7505a
}
.select-number .check .theme3{background:#3598dc
}
.select-number .next .el-button{margin-top:60px;margin-left:20px;background:#f47300;min-width:130px;height:40px;color:#fff
}
.select-number .next .el-button:hover{border-color:#f47201
}


