
.topUp{min-height:560px;margin-top:20px;margin-left:20px;margin-right:25px
}
.topUp .el-row{margin-bottom:65px
}
.topUp li{font-size:14px;color:#999;line-height:1.8em;font-style:italic
}
.topUp .number{font-size:14px;height:37px;line-height:37px
}
.topUp .demo-ruleForm .el-form-item{margin-bottom:12px
}
.topUp .demo-ruleForm .el-form-item .el-form-item__label{color:#999;padding:11px 15px 11px 0
}
.topUp .demo-ruleForm .el-form-item .el-form-item__error{font-size:9px;padding-top:0;padding-bottom:1px
}
.topUp .el-input__inner{border-radius:0;max-width:365px;height:35px
}
.topUp .btnRecharge .el-button{margin-top:35px;background:#f47300;min-width:130px;height:40px;color:#fff
}
.topUp .btnRecharge .el-button:hover{border-color:#f47201
}
.topUp .btnRecharge p{display:inline;margin-left:25px;font-size:14px;text-align:left;height:40px;line-height:40px;color:#333333
}
.topUp .btnRecharge p span{color:#f47300
}
.topUp .topUp-Price .selected-icon{background:url("../../../assets/img/subscri-icon.png") no-repeat;background-position:-109px -37px;position:absolute;top:12px;width:26px;height:30px;display:''
}
.topUp .topUp-Price a{cursor:pointer;padding-top:7px;padding-bottom:7px;padding-left:20px;padding-right:20px;margin-right:2px;font-size:14px;background:#fff;color:#333;height:35px;line-height:35px;border:1px solid #b3c7dc
}


