
.accountInfomation{min-height:715px;padding:30px 20px
}
.accountInfomation .el-row{margin-bottom:10px
}
.accountInfomation .address{margin-bottom:20px;position:relative;top:-20px
}
.accountInfomation .address span{height:25px;line-height:25px;font-size:14px;display:block;margin-top:8px
}
.accountInfomation li{font-size:14px;color:#999;line-height:1.8em;font-style:italic
}
.accountInfomation .info-title{background:url("../../../assets/img/dian.png") no-repeat left center;background-size:6px;font-family:;font-size:14px;color:#333333;height:60px;line-height:60px
}
.accountInfomation .info-title p{padding-left:11px
}
.accountInfomation .info-detail p{margin-right:15px;font-size:14px;color:#999;text-align:right;height:40px;line-height:40px
}
.accountInfomation .info-detail .span{font-size:14px;height:40px;line-height:40px;display:block
}
.accountInfomation .info-detail .btnEdit .el-button{margin-top:45px;background:#f47300;width:130px;height:40px;color:#fff
}
.accountInfomation .info-detail .btnEdit .el-button:hover{border-color:#f47201
}


