
.customerProfile{min-height:715px;padding:30px 20px
}
.customerProfile .el-row{margin-bottom:40px
}
.customerProfile .el-row .address{height:25px;line-height:25px;font-size:14px;display:block;margin-top:10px
}
.customerProfile li{font-size:14px;color:#999;line-height:1.8em;font-style:italic
}
.customerProfile p{margin-right:15px;font-size:14px;color:#999;text-align:right;height:45px;line-height:45px
}
.customerProfile .span{font-size:14px;height:45px;line-height:45px;display:block
}
.customerProfile .btnEdit .el-button{margin-top:70px;background:#f47300;min-width:130px;height:40px;color:#fff
}
.customerProfile .btnEdit .el-button:hover{border-color:#f47201
}
.customerProfile .profileEdit .el-input__inner{border-radius:0;max-width:350px;height:35px;margin-top:5px
}
.customerProfile .profileEdit .demo-ruleForm .el-form-item{margin-bottom:12px
}
.customerProfile .profileEdit .demo-ruleForm .el-form-item .el-form-item__label{color:#999;padding:11px 15px 11px 0
}
.customerProfile .profileEdit .demo-ruleForm .el-form-item .el-form-item__error{font-size:9px;padding-top:0;padding-bottom:1px
}
.customerProfile .profileEdit .el-textarea__inner{border-radius:0;max-width:350px
}
.customerProfile .profileEdit .el-button{margin-top:45px;min-width:95px;height:35px
}
.customerProfile .profileEdit .btnOk{background:#f47300;color:#fff
}
.customerProfile .profileEdit .btnOk:hover{border-color:#f47201
}
.customerProfile .profileEdit .btnCancel{background:#ececec;color:#353535
}


