// 主题样式 默认版
:global {
  html, body, #root {
    margin:0;
    padding:0;
    font-size:@fz-base;
    font-family: @font-family;
  }
  body {
      background: @f;
      font-smoothing: antialiased;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      text-align: left;
      width: 100%;
    .main {
       position: relative;
       width: 100%;
       margin: 0 auto;
    }
    .main-ash{
      background-color: #F6F6F6;
    }
  }
  .content{
    position: relative;
    max-width:@max-width;
    min-width:@min-width;
    width:100%;
    margin: 0 auto;
    padding:0;
    .clearfix;
  }
  .fl{
    float: left;
  }
  .fr{
    float: right;
  }
  .text-l{
    text-align:left;
  }
  .mt-sm{
    display:inline-block;
    margin-top:10px;
  }
  .animated2{
      animation-duration: 1.5s;
      animation-fill-mode:both;
  }
  .el-popover >div,.el-popover__title{
    color:#f47300
  }
  button a{
    color:#fff!important;
  }
  .cd-search-trigger {
      display: none;
  }
  .header{
    position: relative;
    z-index: 1;
    box-shadow:0 1px 15px rgba(162, 158, 158, 0.25);
  }
  .cost{
    color:@title-a-hover!important;
  }
}

.head_row{
  height:@head-row;
  background:@head-row-bg;
  font-size:@fz-base;
  p{
    line-height: @head-row;
    color:@head_row_color;
    font-size:@fz-base;
    cursor:pointer;
  }
  span{
    padding: 0 7px;
    cursor:pointer;
    &:hover{
      color:#fff;
    }
  }
  .el-icon--right{
   transform: scale(0.75);
    font-size:9px;
    margin-left:1px;
  }
  .interval{
    border-right: 1px solid @f;
  }
  .nav-curr{
    span{
    color:#ff6600!important;
    }
    color:#ff6600!important;
  }
}
@{home-banner}{
  display:block;
  width: 100%;
  text-align: center;
  font-size:26px;
  padding:5px 0;
  padding-top: 60px;
  color:#323232;
  z-index: 1;
  .ab-line{
    background:url(@ad-line) no-repeat 10px 45px ;
    background-size: 100%;
    max-width:800px;
    margin:0 auto;
    padding:@ad-line-padding 0 @ad-line-padding 0;
  }
}
.scolumn{
  max-width: 1200px;
  margin:0 auto;
  padding-bottom: 50px;
  text-align:center;
  p{
    color:#555;
    margin-top:@margin-top-sm;
    font-size:@fz-medium;
    &:hover {
      color: #555;
    }
  }
  .summary{
    font-size:0.9em;
    margin-top:8px;
  }
}
.sm-label{
  min-width:65px;
  border-radius:6px;
  display:inline-block;
  background-color:@base-parim-color;
  color:@f;
  vertical-align:top;
  height:27px;
  line-height:27px;
  text-align:center;
  font-size: 16px;
  text-transform: uppercase;
}
// 覆盖
  .el-breadcrumb{
    min-height:80px;
    line-height:80px;
    font-size:18px;
    .el-breadcrumb__item:first-child .el-breadcrumb__item__inner{
      border-left: 3px solid #10a8f8;
      padding-left: 5px;
    }
    .el-breadcrumb__separator,.el-breadcrumb__item__inner,.el-breadcrumb__item:last-child .el-breadcrumb__item__inner, .el-breadcrumb__item:last-child .el-breadcrumb__item__inner a, .el-breadcrumb__item:last-child .el-breadcrumb__item__inner a:hover, .el-breadcrumb__item:last-child .el-breadcrumb__item__inner:hover{
      color:@font-color;
    }
  }
// my-sw
  .mint-swipe-items-wrap > div{
    animation-duration:2s;
    -webkit-animation-duration:2s;
  }
  .my-swipe {
    width: 100%;
    position:relative;
    color: @f;
    text-align:center;
    z-index: 0;
    background:#1678D7;
