      top:-280px;
      z-index: 2;
    }
    .content2{
      max-width: 1200px;
      min-width:768px;
      position: absolute;
      width: 100%;
      height: 489px;
      margin: auto;
      top: 0; left: 0; bottom: 0; right: 0;
