  .mint-swipe .mint-swipe-indicator.is-active{
    opacity: 1;
    border:1px solid transparent;
  }
  .sw-btn{
    position: relative;
    border: 0;
    padding:0;
    margin:0;
    background:transparent;
    top:45%;
    width:57px;
    height:57px;
